
import { Component, Vue } from "vue-property-decorator";
import DishDataService from "@/services/DishDataService";
import Dish from "@/types/Dish";
@Component
export default class Menu extends Vue {
  private dishes: Dish[] = [];
  private currentDish = {} as Dish;
  private currentIndex: number = -1;
  private title: string = "";
  retrieveDishes() {
    DishDataService.getAll()
      .then((response) => {
        this.Dishes = response.data;
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }
  refreshList() {
    this.retrieveDishes();
    this.currentDish = {} as Dish;
    this.currentIndex = -1;
  }
  setActiveDish(dish: Dish, index: number) {
    this.currentDish = Dish;
    this.currentIndex = index;
  }
  removeAllDishes() {
    DishDataService.deleteAll()
      .then((response) => {
        console.log(response.data);
        this.refreshList();
      })
      .catch((e) => {
        console.log(e);
      });
  }
  searchTitle() {
    DishDataService.findByTitle(this.title)
      .then((response) => {
        this.Dishes = response.data;
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }
  mounted() {
    this.retrieveDishes();
  }
}
